@media (min-width: 768px) and (max-width: 979px) {
  .container {
    /* max-width: 870px; */
    flex-direction: column;
    align-items: center;
  }
    .div2 {
      margin: 3% 0 0 0;
    }
}

@media (min-width: 401px) and (max-width: 767px) {
  .container {
    /* max-width: 620px; */
    flex-direction: column;
    align-items: center;


  }
   .div2,
   .div1 {
     width: 400px;
     /* margin: 0% 0 0 0; */
     background-size: 75%;
     background-repeat: no-repeat;
   
   }
}

@media (max-width:400px) {
  .container {
    min-width: 400px;
    flex-direction: column;
    align-items: center;
   


  }

  .div2, .div1 {
    width: 400px;
    margin: 0% 0% 0 0;
    background-size:65% ;
    background-repeat: no-repeat;
   
  }
}



