@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap');

@media (max-width: 980px) {



    .nav-links {
        /* visibility: collapse; */
        transform: translateY(-250%);

    }

    .navbar .nav-links-mobile {
        visibility: visible;
        display: block !important;
        position: absolute;
        top: 90px;

        padding: 0 50%;
        flex-direction: column;
        z-index: 2;
        background-color: white;
        opacity: 0.8;
        max-block-size: fit-content;
        max-inline-size: fit-content;
        max-width: 900px;
        transform: translateY(0);
        transition: all 0.4s;
    }

    .burgerMenu {
        display: flex;
        flex-direction: column;
        background-color: white !important;
        position: absolute;
        top: -5%;
        max-width: 40px !important;
        height: 32px !important;
        right: -135%;




    }

    .header_logo {
        position: absolute;
        top: 7%;
        left: 6%;
        font-family: Playfair Display SC;
        font-style: normal;
        font-weight: normal;
        font-size: 24px;
        line-height: 32px;
        color: #121212;



    }

    .subscribe {
        position: absolute;
        top: 7%;
        right: 20%;
        margin-right: 5%;
padding: 0%;


    }

    .about,
    .contact,
    .blog,
    .accessories,
    .beauty,
    .men,
    .women {
        display: flex;
        text-decoration: none;
        font-family: Spartan;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        color: #121212 !important;
        /* max-block-size: fit-content; */
        max-inline-size: fit-content;
        max-width: 900px;


    }


    .span {

        background-color: rgb(144, 144, 144) !important;
        width: 32px !important;
        height: 2px !important;
        margin: 4px !important;
        border-radius: 2px !important;
    }

    .open1 {
        transform: rotate(45deg) translateY(14px) !important;
    }

    .open2 {
        opacity: 0;
    }

    .open3 {
        transform: rotate(-45deg) translateY(-14px) !important;
    }

    .close1 {
        transform: rotate(0);
    }

    .close2 {
        opacity: 1;
    }

    .close3 {
        transform: rotate(0);
    }


}

